.ng2-c-checkbox__gui {
  border: 1px solid #cccccc;
  border-radius: 50%;
  box-sizing: border-box;
  display: block;
  height: 24px;
  position: relative;
  text-align: center;
  width: 24px; }

.ng2-c-checkbox {
  display: inline-block;
  min-width: 24px;
  vertical-align: middle; }

.ng2-c-checkbox__label {
  align-items: center;
  display: flex; }

.ng2-c-checkbox__label--rtl {
  flex-direction: row-reverse; }

.ng2-c-checkbox__label-text {
  margin: 0 10px; }

.ng2-c-checkbox__gui--checked {
  background-color: #6ec60f;
  border: 0; }

/*
    --disabled must come after --checked because checkboxes can be both checked and disabled
    in which case --disabled styles should override --checked styles.
*/
.ng2-c-checkbox__gui--disabled {
  background-color: #cccccc; }

/* bd-checkbox uses a real <input type="checkbox"> that is invisible but still clickable */
.ng2-c-checkbox__input {
  bottom: 0;
  height: inherit;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: inherit; }

.ng2-c-checkbox__icon {
  color: #ffffff;
  font-size: 13px;
  line-height: 24px; }
